
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #012326;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logopicture {
  margin-left: '500';
}

.otsikko {
  font-size: 100px;
  font-style:italic;
  color: white;
  padding: 200px;
}

.perus {
  color: white;
  padding: 30px;
}

@font-face {
  font-family: 'RSOH1';
  src: url('./pictures/racing-sans-one.regular.ttf');
  padding: 10px
}

.dropdown-container {
  width: 300px;
  color: white;
  padding: 10px;
}

.black {
  color: black
}

.white {
  color: white
}

.selectandbuytyres {
  font-family: 'RSOH1';
  src: url('./pictures/racing-sans-one.regular.ttf');
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: center;
  margin: 15px;
}

.flexrow {
  font-family: 'RSOH1';
  src: url('./pictures/racing-sans-one.regular.ttf');
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: top;
}


.videoupotus {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  padding: 15px
}

.headerbanner {
  justify-content: center;
  display:flex;
  flex-direction:column;
  align-items: center;
  font-family: 'RSOH1';
  font-size: 30px;
  color: white;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.center-right {
  position: absolute;
  bottom: 400px;
  right: 400px;
}

.boxrow {
  font-family: 'RSOH1';
  src: url('./pictures/racing-sans-one.regular.ttf');
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  margin: 15px;
}